import "../css/frontpage.css"
import "../css/top-container.css"
import logo from "../images/logo.PNG"
import notifications from "../images/notification.png"
import inventory from "../images/inventory.jpg"
import enviroment from "../images/enviromental-values.png"

export default function Hjem() {
    return <>
        <div className="frontpage-container top-container">
            <div>
                <h1>Skab vkst, innovation og bredygtighed med vores eksperter</h1>
            </div>
            <img id="frontpage-logo" src={logo} alt="Firm Logo" />
            <div>
                <h4> Passion omkring dyrkning af planter og skabelsen af naturlig sknhed og bredygtighed. Vores vksthus er fyldt med en mangfoldighed af planter, der er omhyggeligt dyrket og plejet af vores dedikerede gartnere.        </h4>
            </div>
        </div>
        <div className="more-information">
            <div className="circle-div">
                <img src={inventory} alt="Lagerbeholdning-billede" className="circle-image" />
                <p className="info-text">F overblik over lagerbeholdningen</p>
            </div>
            <div className="circle-div">
                <img src={enviroment} alt="Miljvrdier-billede" className="circle-image" />
                <p className="info-text">Administrer og monitorr aktuelle miljforhold</p>
            </div>
            <div className="circle-div">
                <img src={notifications} alt="Notifikationer-billede" className="circle-image" />
                <p className="info-text">Hold dig informeret om opdateringer og pmindelser</p>
            </div>
            </div>
        </>
}